let Typed = require('typed.js');
let Mice = require('mousetrap')
import { Howl, Howler } from 'howler/src/howler.core'

let soundFile = new URL("../static/files/easter-egg-coin.wav", import.meta.url)
const sound = new Howl({
    src: [soundFile],
    html5: true
});
sound.volume(0.4);

// combinations
Mousetrap.bind('command+option+1', function () {
    toggleGame();
    return false;
});
// combinations
Mousetrap.bind('ctrl+option+1', function () {
    toggleGame();
    return false;
});


Mousetrap.bind('space', function (e) {
    if (e.preventDefault) {
        e.preventDefault();
    } else {
        // internet explorer
        e.returnValue = false;
    }
});


